







import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
  name: 'Notes',
  computed: {
    ...mapState(['notes']),
    noteText: {
      get() {
        return this.notes;
      },
      set(val) {
        this.$store.dispatch('setNotes', val);
      },
    },
  },
});
