





















import Vue from 'vue';
import { DiceRoller } from 'rpg-dice-roller';

const roller = new DiceRoller();

export default Vue.extend({
  name: 'DiceRoller',
  data() {
    return {
      diceString: '',
      diceResult: '',
    };
  },
  methods: {
    roll(): void {
      this.clearIfEmpty();
      const result = roller.roll(this.diceString);
      this.diceResult = result.toString();
    },
    clearIfEmpty(): void {
      if (!this.diceString) {
        this.diceResult = '';
      }
    },
  },
});
